'use client';

import { useState } from 'react';
import { signIn } from 'next-auth/react';
import { useRouter } from 'next/navigation';
import { LockClosedIcon, EnvelopeIcon } from '@heroicons/react/24/outline';

export default function AdminLoginPage() {
  const router = useRouter();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    try {
      // Using the standard NextAuth sign-in function
      const result = await signIn('credentials', {
        redirect: false, // We handle the redirect manually
        email,
        password,
      });

      if (result?.ok) {
        // On success, NextAuth has set the session cookie.
        // Now we can safely redirect to the admin dashboard.
        router.push('/admin');
      } else {
        // If signIn fails, it returns a user-friendly error message.
        setError(result?.error || 'Invalid credentials.');
        setLoading(false);
      }
    } catch (err) {
      console.error('Login submission error:', err);
      setError('An unexpected error occurred.');
      setLoading(false);
    }
  };

  return (
    <div className="flex items-center justify-center min-h-screen bg-gray-900 text-white">
      <div className="w-full max-w-md p-8 space-y-8 bg-gray-800 rounded-lg shadow-2xl">
        <div>
          <h2 className="text-3xl font-extrabold text-center text-pink-500">
            Admin Access
          </h2>
          <p className="mt-2 text-center text-gray-400">
            Dunkin' Feedback Management
          </p>
        </div>
        <form className="space-y-6" onSubmit={handleSubmit}>
          <div className="relative">
            <EnvelopeIcon className="absolute h-5 w-5 text-gray-500 top-3.5 left-4" />
            <input
              type="email"
              placeholder="Email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="w-full pl-12 pr-4 py-3 bg-gray-700 border border-gray-600 rounded-md focus:ring-2 focus:ring-pink-500 focus:outline-none"
              required
            />
          </div>
          <div className="relative">
            <LockClosedIcon className="absolute h-5 w-5 text-gray-500 top-3.5 left-4" />
            <input
              type="password"
              placeholder="Password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="w-full pl-12 pr-4 py-3 bg-gray-700 border border-gray-600 rounded-md focus:ring-2 focus:ring-pink-500 focus:outline-none"
              required
            />
          </div>
          {error && <p className="text-red-400 text-sm text-center">{error}</p>}
          <button
            type="submit"
            disabled={loading}
            className="w-full py-3 font-semibold text-white bg-pink-600 rounded-md hover:bg-pink-700 disabled:bg-pink-800 disabled:cursor-not-allowed transition-all"
          >
            {loading ? 'Authenticating...' : 'Sign In'}
          </button>
        </form>
      </div>
    </div>
  );
} 